<template>
  <div>
    <el-dialog
      title="编辑"
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose"
      append-to-body
    >
      <el-form
        ref="editForm"
        :model="editForm"
        :rules="rules"
        label-width="100px"
      >
        <el-form-item label="字段名称" prop="prop">
          <el-input v-model="editForm.prop" disabled size="small"></el-input>
        </el-form-item>
        <el-form-item label="字段备注" prop="label">
          <el-input v-model="editForm.label" disabled size="small"></el-input>
        </el-form-item>
        <el-form-item label="序号" prop="order_number">
          <el-input-number
            v-model="editForm.order_number"
            :min="0"
            :precision="0"
            style="width: 100%"
            size="small"
          ></el-input-number>
        </el-form-item>
        <el-form-item label="列宽" prop="width">
          <el-input-number
            v-model="editForm.width"
            :min="0"
            :precision="0"
            style="width: 100%"
            size="small"
          ></el-input-number>
        </el-form-item>
        <el-form-item label="位置" prop="align">
          <el-select
            v-model="editForm.align"
            clearable
            filterable
            size="small"
            style="width: 100%"
          >
            <el-option label="居左" value="left"></el-option>
            <el-option label="居中" value="center"></el-option>
            <el-option label="居右" value="right"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          v-show="!flag"
          label="查询字段"
          prop="default_query"
          size="small"
        >
          <el-radio-group v-model="editForm.default_query" size="small">
            <el-radio :label="1">是</el-radio>
            <el-radio :label="0">否</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item
          :label="flag ? '是否可编辑' : '列表显示'"
          prop="default_show"
        >
          <el-radio-group v-model="editForm.default_show" size="small">
            <el-radio :label="1">是</el-radio>
            <el-radio :label="0">否</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" size="small" @click="save">保 存</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { updateColumnShow } from "@/api";
export default {
  data() {
    return {
      dialogVisible: false,
      editForm: {},
      rules: {
        width: { required: true, trigger: "blur", message: "请输入列宽" },
        align: { required: true, trigger: "change", message: "请选择位置" },
        default_query: {
          required: true,
          message: "请选择是否设置为查询条件",
          trigger: "change",
        },
        default_show: {
          required: true,
          message: "请选择是否在列表中显示",
          trigger: "change",
        },
        order_number: {
          required: true,
          message: "请输入序号",
          trigger: "change",
        },
      },
      flag: false,
    };
  },
  methods: {
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(() => {
          done();
        })
        .catch(() => {});
    },
    show(row, flag) {
      this.flag = flag != 0 ? true : false;
      this.dialogVisible = true;
      this.editForm = { ...row };
    },
    save() {
      this.$refs.editForm.validate(async (valid) => {
        if (valid) {
          const { state, msg } = await updateColumnShow(this.editForm);
          this.$message({
            message: msg,
            type: state,
          });
          if (state == "success") {
            this.dialogVisible = false;
            this.$emit("update");
          }
        } else {
          this.$message({
            message: "请输入必填项",
            type: "info",
          });
        }
      });
      // .then(() => {

      // });
    },
  },
};
</script>

<style lang="scss" scoped></style>
