          <el-dropdown size="small" @command="handleNewFieldCommand">
            <el-button type="primary" size="small">
              新建字段
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="IntegerWE">
                整数 / Integer
              </el-dropdown-item>
              <el-dropdown-item command="DecimalWE">
                精度小数 / Decimal
              </el-dropdown-item>

              <el-dropdown-item command="TextWE" divided>
                文本 / Text
              </el-dropdown-item>
              <!--
              <el-dropdown-item command="EmailWE">邮箱 / Email</el-dropdown-item>
              <el-dropdown-item command="UrlWE">网址 / Url</el-dropdown-item>
              -->
              <el-dropdown-item
                v-if="entityProps.is_childtable != 2"
                command="TextAreaWE"
              >
                长文本 / TextArea
              </el-dropdown-item>
              <!--
              <el-dropdown-item command="PasswordWE">密码 / Password</el-dropdown-item>
              -->
              <el-dropdown-item
                v-if="entityProps.is_childtable != 2"
                command="RadioWE"
                divided
              >
                单选项 / Radio
              </el-dropdown-item>
              <el-dropdown-item
                v-if="entityProps.is_childtable != 2"
                command="CheckboxWE"
                >多选项 / Checkbox</el-dropdown-item
              >
              <el-dropdown-item command="SelectWE" divided>
                下拉框(单选) / Select
              </el-dropdown-item>
              <el-dropdown-item command="MultipleWE"
                >下拉框(多选) / Multiple</el-dropdown-item
              >
              <el-dropdown-item command="DateWE" divided>
                日期 / yyyy-MM-dd
              </el-dropdown-item>
              <el-dropdown-item command="DateTimeWE">
                日期时间 / yyyy-MM-dd HH:mm:ss
              </el-dropdown-item>
              <el-dropdown-item command="YearWE">年份 / yyyy</el-dropdown-item>
              <el-dropdown-item command="MonthWE">
                年月 / yyyy-MM
              </el-dropdown-item>
              <el-dropdown-item command="TimeWE">
                时间 / HH:mm:ss
              </el-dropdown-item>
              <!--              <el-dropdown-item command="FileWE" divided>-->
              <!--                文件 / File-->
              <!--              </el-dropdown-item>-->
              <!--              <el-dropdown-item command="PictureWE" divided>-->
              <!--                图片 / Picture-->
              <!--              </el-dropdown-item>-->
              <el-dropdown-item
                v-if="entityProps.is_childtable != 2"
                command="SignWE"
                divided
              >
                电子签名 / Sign
              </el-dropdown-item>
              <!--
              <el-dropdown-item command="AnyReferenceWE">一对多引用 / AnyReference</el-dropdown-item>
              <el-dropdown-item command="ReferenceListWE">多对多引用 / ReferenceList</el-dropdown-item>
              -->
            </el-dropdown-menu>
          </el-dropdown>
        </div>
        <div class="search-panel-right">
          <el-input
            v-model="searchText"
            type="text"
            :clearable="true"
            class="v-middle"
            size="small"
            placeholder="请输入关键词搜索"
            @clear="clearTableSearch"
            @keyup.enter.native="searchTableData"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="searchTableData"
            ></el-button>
          </el-input>
        </div>
      </el-header>

      <el-main ref="tableContainer">
        <div style="height: 100%">
          <SimpleTable
            ref="SimpleTable"
            :columns="columns"
            :data="filteredData"
            :show-pagination="false"
            :show-check-box="false"
            :height="tableHeight - 126 + ''"
            table-size="small"
            table-width="100% !important"
          >
            <el-table-column
              slot="table_operation"
              align="center"
              label="操作"
              width="130"
              :resizable="false"
              fixed="right"
            >
              <template v-if="scope.row.is_system == 0" slot-scope="scope">
                <el-button
                  type="text"
                  size="mini"
                  icon="el-icon-edit"
                  @click="editTableData(scope.row)"
                >
                  修改
                </el-button>
                <el-button
                  type="text"
                  size="mini"
                  icon="el-icon-delete-solid"
                  @click="deleteTableData(scope.row)"
                >
                  删除
                </el-button>
              </template>
            </el-table-column>
          </SimpleTable>
          <el-pagination
            :current-page="page.pageNum"
            :page-sizes="[10, 20, 30, 100]"
            :page-size="page.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="page.total"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
          ></el-pagination>
        </div>
      </el-main>

      <el-footer
        style="
          height: 32px;
          line-height: 32px;
          font-size: 13px;
          background-color: white;
        "
      >
        <div>字段列表：当前实体共 {{ tableData.length }} 个字段</div>
      </el-footer>

      <el-dialog
        v-if="showNameFieldDialogFlag"
        title="修改名称字段"
        :visible.sync="showNameFieldDialogFlag"
        :append-to-body="true"
        :destroy-on-close="true"
        class="name-field-dialog"
        width="460px"
      >
        <div class="name-field-hint">
          <i class="el-icon-bell"></i>
          提示：只有文本(Text)类型字段可设置为名称字段。
        </div>
        <SimpleTable
          :show-pagination="false"
          :show-check-box="false"
          :table-size="'mini'"
          :columns="nameFieldColumns"
          :data="nameFieldData"
          :max-height="420"
        >
          <el-table-column
            slot="table_operation"
            align="center"
            label=""
            width="150"
            :resizable="false"
          >
            <template slot-scope="scope">
              <el-button
                v-if="!!!scope.row.nameFieldFlag"
                class=""
                icon="el-icon-check"
                @click="selectNameField(scope.row)"
              >
                选择
              </el-button>
              <el-button v-else :disabled="true">当前名称字段</el-button>
            </template>
          </el-table-column>
        </SimpleTable>
      </el-dialog>

      <el-dialog
        v-if="showNewFieldDialogFlag"
        :title="'新建字段 / ' + curEditorType"
        :visible.sync="showNewFieldDialogFlag"
        :show-close="true"
        :destroy-on-close="true"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        class="no-padding"
        width="620px"
        append-to-body
      >
        <component
          :is="curFWEditor"
          :entity="entity"
          :showing-in-dialog="true"
          @fieldSaved="onFieldSaved"
          @cancelSave="onCancelSaveField"
        ></component>
      </el-dialog>

      <el-dialog
        v-if="showEditFieldDialogFlag"
        :title="'编辑字段 / ' + curEditorType"
        :visible.sync="showEditFieldDialogFlag"
        :show-close="true"
        :destroy-on-close="true"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        class="no-padding"
        width="620px"
        append-to-body
      >
        <component
          :is="curFWEditor"
          ref="editField"
          :entity="entity"
          :showing-in-dialog="true"
          :field-name="editingFieldName"
          :field-state="2"
          @fieldSaved="onFieldSaved"
          @cancelSave="onCancelSaveField"
        ></component>
      </el-dialog>

      <el-dialog
        v-if="showEntityPropsDialogFlag"
        title="编辑实体属性"
        :visible.sync="showEntityPropsDialogFlag"
        :show-close="false"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        :destroy-on-close="true"
        append-to-body
      >
        <EntityPropEditor
          ref="EPEditor"
          :entity-props="entityProps"
          :show-title="false"
          :filter-entity-method="filterMainEntity"
        ></EntityPropEditor>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" size="small" @click="saveEntity">
            保 存
          </el-button>
          <el-button size="small" @click="cancelSaveEntity">取 消</el-button>
        </div>
      </el-dialog>
    </el-container>
  </el-container>
</template>

<script>
// import {
//   getEntityProps, getFieldListOfEntity, getEntitySet, updateEntityLabel, getTextFieldList,
//   updateEntityNameField, fieldCanBeDeleted, deleteField, fieldCanBeEdited
// } from '@/api/system-manager'
import { formatBooleanColumn, copyNew, formatTypeColumn } from "@/utils/util";
import EntityPropEditor from "@/views/system/entity-editor/entity-property-editor";
import { Message } from "element-ui";
import { getFieldList, getFieldListOwn, saveFactTable, delField } from "@/api";

export default {
  name: "EntityFieldTable",
  components: {
    EntityPropEditor,
    BooleanWE: () =>
      import("@/views/system/field-editor/boolean-widget-editor"),
    IntegerWE: () =>
      import("@/views/system/field-editor/integer-widget-editor"),
    DecimalWE: () =>
      import("@/views/system/field-editor/decimal-widget-editor"),
    PercentWE: () =>
      import("@/views/system/field-editor/percent-widget-editor"),
    MoneyWE: () => import("@/views/system/field-editor/money-widget-editor"),

    TextWE: () => import("@/views/system/field-editor/text-widget-editor"),
    EmailWE: () => import("@/views/system/field-editor/email-widget-editor"),
    UrlWE: () => import("@/views/system/field-editor/url-widget-editor"),
    TextAreaWE: () =>
      import("@/views/system/field-editor/textarea-widget-editor"),

    RadioWE: () => import("@/views/system/field-editor/radio-widget-editor"),
    CheckboxWE: () =>
      import("@/views/system/field-editor/checkbox-widget-editor"),

    SelectWE: () => import("@/views/system/field-editor/select-widget-editor"),
    MultipleWE: () =>
      import("@/views/system/field-editor/multiple-widget-editor"),

    DateWE: () => import("@/views/system/field-editor/date-widget-editor"),
    DateTimeWE: () =>
      import("@/views/system/field-editor/datetime-widget-editor"),
    TimeWE: () => import("@/views/system/field-editor/time-widget-editor"),
    YearWE: () => import("@/views/system/field-editor/year-widget-editor"),
    MonthWE: () => import("@/views/system/field-editor/month-widget-editor"),
    PictureWE: () =>
      import("@/views/system/field-editor/picture-widget-editor"),
    FileWE: () => import("@/views/system/field-editor/file-widget-editor"),
    SignWE: () => import("@/views/system/field-editor/sign-widget-editor"),
    // password-widget-editor
    ReferenceWE: () =>
      import("@/views/system/field-editor/reference-widget-editor"),
    AnyReferenceWE: () =>
      import("@/views/system/field-editor/anyreference-widget-editor"),
    ReferenceListWE: () =>
      import("@/views/system/field-editor/referencelist-widget-editor"),
  },
  props: {
    // entity: {
    //   type: Object,
    //   default() {
    //     return {};
    //   },
    // },
  },
  data() {
    return {
      entity: {},
      columns: [
        {
          prop: "order_number",
          label: "序号",
          width: "60",
          align: "left",
          fixed: true,
        },
        {
          prop: "name",
          label: "字段名称",
          width: "130",
          align: "left",
          fixed: true,
        },
        { prop: "label", label: "显示名称", align: "center" },
        // { prop: "physicalName", label: "数据库字段名", align: "left" },
        {
          prop: "type",
          label: "字段类型",
          width: "100",
          align: "left",
          formatter: "formatTypeColumn",
        },
        // { prop: "referTo", label: "引用实体", width: "120", align: "center" },
        {
          prop: "table_name",
          label: "数据库表名",
          align: "center",
        },

        {
          prop: "is_system",
          label: "系统字段",
          width: "70",
          align: "center",
          formatter: "formatBooleanColumn",
        },
      ],
      tableData: [],
      filteredData: [],
      searchText: "",
      tableHeight: 400,

      curFWEditor: "",
      curEditorType: "",
      showNameFieldDialogFlag: false,
      showNewFieldDialogFlag: false,
      showEditFieldDialogFlag: false,
      showEntityPropsDialogFlag: false,
      editingFieldName: null,

      nameFieldColumns: [
        {
          prop: "name",
          label: "字段名称",
          width: "148",
          align: "left",
          fixed: true,
        },
        { prop: "label", label: "显示名称", width: "148", align: "center" },
      ],
      nameFieldData: [],
      page: {
        pageNum: 1,
        pageSize: 10,
        total: 0,
      },
      entityProps: {
        name: "Account",
        label: "客户",
        entityCode: 0,
        physicalName: "",
        nameField: "",
        detailEntityFlag: false,
        mainEntity: "",
        layoutable: true,
        listable: true,
        authorizable: false,
        assignable: false,
        shareable: false,
      },
      entityCopy: {},
      entityItems: [],
    };
  },
  mounted() {
    window.onresize = () => {
      this.resizeTableHeight();
    };
  },
  methods: {
    filterMainEntity(filterList, callBack) {
      filterList.length = 0; /* 清空数组，不能用filterList=[]，否则SimpleTable显示不出数据！！ */
      this.entityItems.filter((entity) => {
        if (
          entity.is_childtable === 0 &&
          this.entityProps.name != entity.name
        ) {
          filterList.push({
            name: entity.name,
            label: entity.label,
            id: entity.id,
          });
          // console.log(filterList);
        }
      });
      callBack();
    },
    resizeTableHeight() {
      /* table自适应高度 */
      // this.tableHeight =
      //   this.$refs.tableContainer.$el.offsetHeight + 100; /*覆盖表格页脚高度*/
      // console.log(this.tableHeight, "height");
    },

    initPageData() {
      this.resizeTableHeight();
      this.initTableData();
    },

    async initTableData() {
      this.$refs.SimpleTable.columns = this.columns;
      const { t, state, msg } = await getFieldListOwn({
        pid: this.entity.id,
        val: this.searchText,
        pageNum: this.page.pageNum,
        pageSize: this.page.pageSize,
      });
      if (state == "success") {
        this.tableData = this.filteredData = t.list;
        this.page.total = t.total;
        this.$refs.SimpleTable.updatable(t.list);
        // console.log(this.$refs.SimpleTable);
      } else {
        this.$message({ message: msg, type: state });
      }

      // getFieldListOfEntity(this.entity)
      //   .then((res) => {
      //     if (res.error != null) {
      //       this.$message({ message: res.error, type: "error" });
      //       return;
      //     }
      //     this.tableData = res.data;
      //     this.filteredData = copyNew(this.tableData);
      //     this.searchText = "";
      //   })
      //   .catch((res) => {
      //     this.$message({ message: res.message, type: "error" });
      //   });
    },

    handleNewFieldCommand(command) {
      this.curFWEditor = command;
      const typeObj = {
        Checkbox: "多选框",
        Date: "日期(yyyy-MM-dd)",
        DateTime: "日期时间(yyyy-MM-dd HH:mm:ss)",
        Decimal: "小数",
        File: "文件",
        Integer: "整数",
        Month: "年月(yyyy-MM)",
        Multiple: "多选下拉框",
        Picture: "图片",
        Radio: "单选框",
        Select: "单选下拉框",
        Sign: "电子签名",
        Text: "文本",
        Textarea: "大文本",
        Time: "时间(HH:mm:ss)",
        Year: "年份(yyyy)",
      };
      this.curEditorType = typeObj[command.replace(/WE$/, "")];
      this.showNewFieldDialogFlag = true;
    },

    editTableData(row) {
      if (row.is_system != 1) {
        const typeObj = {
          1: { name: "Integer", label: "整数" },
          2: { name: "Decimal", label: "小数" },
          3: { name: "Text", label: "文本" },
          4: { name: "Textarea", label: "大文本" },
          5: { name: "Select", label: "单选下拉框" },
          6: { name: "Multiple", label: "多选下拉框" },
          7: { name: "Date", label: "日期(yyyy-MM-dd)" },
          8: { name: "DateTime", label: "日期时间(yyyy-MM-dd HH:mm:ss)" },
          9: { name: "Year", label: "年份(yyyy)" },
          10: { name: "Month", label: "年月(yyyy-MM)" },
          11: { name: "Time", label: "时间(HH:mm:ss)" },
          12: { name: "File", label: "文件" },
          13: { name: "Picture", label: "图片" },
          14: { name: "Radio", label: "单选框" },
          15: { name: "Checkbox", label: "多选框" },
          16: { name: "Sign", label: "电子签名" },
        };
        this.curEditorType = typeObj[row.type].label;
        this.curFWEditor = typeObj[row.type].name + "WE";
        this.editingFieldName = row.name;
        // this.entity = JSON.parse(JSON.stringify(row));
        this.showEditFieldDialogFlag = true;
        setTimeout(() => {
          this.$nextTick(() => {
            if (row.data_code) this.$refs.editField.getDtList(row.data_code);

            this.$refs.editField.fieldProps = JSON.parse(JSON.stringify(row));
          });
        }, 500);
      } else {
        this.$message.info("提示：系统字段/保留字段不能编辑！");
      }
    },

    deleteTableData(row) {
      if (!!this.entity && !!row.type && !!row.name) {
        let confirmText = [
          "字段删除后不能恢复，是否确认删除?",
          "1. 删除字段会清空该字段的所有数据，且不能恢复；’",
          "2. 删除字段不会同步更新表单和列表，请手工从表单布局和列表中移除；",
        ];
        const h = this.$createElement;
        let pTags = [];
        confirmText.forEach((ct) => {
          pTags.push(h("p", null, ct));
        });
        this.$confirm("删除提醒", {
          message: h("div", null, pTags),
          showCancelButton: true,
          type: "warning",
        })
          .then(() => {
            this.deleteField(row);
          })
          .catch(() => {
            this.$message({ type: "info", message: "已取消删除" });
          });
      }
    },

    deleteField(row) {
      delField(row)
        .then((res) => {
          if (res.state == "error") {
            this.$message({ message: res.error, type: "error" });
            return;
          }
          this.$message.success("字段已删除");
          this.initTableData();
        })
        .catch((res) => {
          this.$message({ message: res.message, type: "error" });
        });
    },

    onFieldSaved() {
      this.showNewFieldDialogFlag = false;
      this.showEditFieldDialogFlag = false;
      this.initTableData();
    },

    onCancelSaveField() {
      this.showNewFieldDialogFlag = false;
      this.showEditFieldDialogFlag = false;
    },

    modifyEntity() {
      this.showEntityPropsDialogFlag = true;
      this.entityCopy = { ...this.entityProps };
    },

    async saveEntity() {
      if (!this.$refs["EPEditor"].validateForm()) return;
      const { state, msg } = await saveFactTable(this.entityProps);
      if (state == "error") {
        this.$message({ message: msg, type: "error" });
        return;
      }
      this.$message.success("保存成功");
      this.showEntityPropsDialogFlag = false;
      this.$parent.$parent.$parent.selectedEntityObj = {
        ...this.entityProps,
      };
      // this.getEntityList();
    },

    cancelSaveEntity() {
      this.entityProps = { ...this.entityCopy };
      console.log(this.entityProps);
      console.log(this.entity);
      console.log(this.entityCopy);
      this.showEntityPropsDialogFlag = false;
    },

    modifyEntityLabel() {
      this.$prompt("修改实体显示名称", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        inputValue: this.entityProps.label,
        inputPattern:
          /^[A-Za-z\u4e00-\u9fa5\d]+$/ /* 匹配由字母大小写、汉字或数字组成的字符串 */,
        inputErrorMessage: "输入不正确",
      })
        .then(async ({ value }) => {
          const postData = { ...this.entityProps };
          postData.label = value;
          const { state, msg } = await saveFactTable(postData);
          if (state == "success") {
            this.entityProps.label = value;
            this.$message({ message: "修改成功", type: "success" });
          } else {
            this.$message({ message: msg, type: state });
          }
          // updateEntityLabel(this.entityProps.name, value)
          //   .then((res) => {
          //     if (res.error != null) {
          //       this.$message({ message: res.error, type: "error" });
          //       return;
          //     }

          //   })
