<template>
  <div ref="tableContainer" style="padding: 10px">
    <div style="text-align: right;">
      <el-form :model="page" inline size="small" @submit.native.prevent>
        <el-form-item label="字段名/字段中文名">
          <el-input v-model="page.val" @keyup.enter.native="getList"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getList">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <el-table
      ref="table"
      :data="tableData"
      border
      stripe
      tooltip-effect="light"
      :header-cell-style="{ background: '#f6f8f9' }"
    >
      <template v-for="(item, index) in columns">
        <el-table-column
          v-if="!item.hidden"
          :key="index"
          :fixed="item.fixed"
          :prop="item.prop"
          :label="item.label"
          :align="item.align ? item.align : 'center'"
          :width="item.width"
          :show-overflow-tooltip="true"
        >
          <template #default="{ row }">
            <span
              v-html="
                item && item.formatter
                  ? formatter(row, row[item.prop], item)
                  : formatterValue(row, row[item.prop], item)
              "
            ></span>
          </template>
        </el-table-column>
      </template>
      <el-table-column
        align="center"
        label="操作"
        width="130"
        :resizable="false"
        fixed="right"
      >
        <template slot-scope="scope">
          <el-button
            v-if="!scope.row.is_system"
            type="text"
            size="mini"
            icon="el-icon-edit"
            @click="editTableData(scope.row)"
          >
            修改
          </el-button>
          <!-- <el-button
            type="text"
            size="mini"
            icon="el-icon-delete-solid"
            @click="deleteTableData(scope.row)"
          >
            删除
          </el-button> -->
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
      :current-page="page.pageNum"
      :page-sizes="[10, 20, 30, 100]"
      :page-size="page.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    ></el-pagination>
    <listEdit ref="listEdit" @update="getList"></listEdit>
    <div style="text-align: right">
      <el-button @click="$emit('close')">关 闭</el-button>
    </div>

  </div>
</template>

<script>
import listEdit from "./editForm";
import { formatBooleanColumn } from "@/utils/util";
import { getColumnShow } from "@/api";
import * as format from "@/utils/util";
export default {
  components: { listEdit },
  data() {
    return {
      tableData: [],
      entity: {},
      page: {
        pageSize: 10,
        pageNum: 1,
        val:'',
      },
      total: 0,
      tableHeight: 400,
      columns: [
        {
          prop: "order_number",
          label: "显示顺序",
          align: "center",
          width:80
        },
        {
          prop: "prop",
          label: "字段名称",
          align: "center",
        },
        {
          prop: "label",
          label: "字段备注",
          align: "center",
        },
        {
          prop: "width",
          label: "列宽",
          align: "center",
        },
        {
          prop: "align",
          label: "位置",
          align: "center",
        },
        {
          prop: "default_show",
          label: "列表显示",
          align: "center",
          formatter: "formatBooleanColumn",
        },
        {
          prop: "default_query",
          label: "查询字段",
          align: "center",
          formatter: "formatBooleanColumn",
        },
        // {
        //   prop: "table_operation",
        //   type: "table_operation",
        //   label: "操作",
        //   align: "center",
        // },
      ],
    };
  },
  methods: {
    formatterValue(row, cellValue) {
      return cellValue;
    },
    formatter(row, cellValue, item) {
      if (item.formatter && typeof item.formatter == "string") {
        const methods = this.$options.methods;
        if(methods[item.formatter]){
          return methods[item.formatter](cellValue);
        }else {
          return format[item.formatter](row, cellValue, item)
        }

      }
    },
    resizeTableHeight() {
      /* table自适应高度 */
      // this.tableHeight =
      //   this.$refs.tableContainer.offsetHeight - 42 + 42; /*覆盖表格页脚高度*/
      // alert(this.tableHeight);
    },
    getList() {
      if (this.entity.is_maintable == 1) {
        this.columns[5].label = "是否可编辑";
        this.columns[6].hidden = true;
      } else {
        this.columns[5].label = "列表显示";
        this.columns[6].hidden = false;
      }

      getColumnShow({
        table_id: this.entity.id,
        pageNum: this.page.pageNum,
        pageSize: this.page.pageSize,
        val:this.page.val
      })
        .then((res) => {
          if (res.state == "error") {
            this.$message({
              message: res.msg,
              type: "error",
            });
            return;
          }
          this.tableData = res.t.list;
          this.total = res.t.total;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    editTableData(row) {
      this.$refs.listEdit.show(row, this.entity.is_childtable);
    },
    handleSizeChange(val) {
      this.page.pageSize = val;
      this.getList();
    },
    handleCurrentChange(val) {
      this.page.pageNum = val;
      this.getList();
    },
  },
};
</script>

<style lang="scss" scoped></style>
