<script>
import { importFields } from "@/api";

export default {
  components: {
    IntegerWE: () =>
      import("@/views/system/field-editor/integer-widget-editor"),
    DecimalWE: () =>
      import("@/views/system/field-editor/decimal-widget-editor"),
    PercentWE: () =>
      import("@/views/system/field-editor/percent-widget-editor"),
    MoneyWE: () => import("@/views/system/field-editor/money-widget-editor"),

    TextWE: () => import("@/views/system/field-editor/text-widget-editor"),
    EmailWE: () => import("@/views/system/field-editor/email-widget-editor"),
    UrlWE: () => import("@/views/system/field-editor/url-widget-editor"),
    TextAreaWE: () =>
      import("@/views/system/field-editor/textarea-widget-editor"),

    RadioWE: () => import("@/views/system/field-editor/radio-widget-editor"),
    CheckboxWE: () =>
      import("@/views/system/field-editor/checkbox-widget-editor"),

    SelectWE: () => import("@/views/system/field-editor/select-widget-editor"),
    MultipleWE: () =>
      import("@/views/system/field-editor/multiple-widget-editor"),

    DateWE: () => import("@/views/system/field-editor/date-widget-editor"),
    DateTimeWE: () =>
      import("@/views/system/field-editor/datetime-widget-editor"),
    TimeWE: () => import("@/views/system/field-editor/time-widget-editor"),
    YearWE: () => import("@/views/system/field-editor/year-widget-editor"),
    MonthWE: () => import("@/views/system/field-editor/month-widget-editor"),
    PictureWE: () =>
      import("@/views/system/field-editor/picture-widget-editor"),
    FileWE: () => import("@/views/system/field-editor/file-widget-editor"),
    SignWE: () => import("@/views/system/field-editor/sign-widget-editor"),
  },
  props: {
    entity: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      pageInfo: {
        pageSize: 30,
        pageNum: 1,
        total: 0,
        value: "",
      },
      searchVal: "",
      selectedEntityObj: {},
      fieldsTable: [],
      fieldType: [
        { val: 1, label: "整数", name: "IntegerWE" },
        { val: 2, label: "小数", name: "DecimalWE" },
        { val: 3, label: "文本", name: "TextWE" },
        { val: 4, label: "大文本", name: "TextAreaWE" },
        { val: 5, label: "单选下拉框", name: "SelectWE" },
        { val: 6, label: "多选下拉框", name: "MultipleWE" },
        { val: 7, label: "日期(yyyy-MM-dd)", name: "DateWE" },
        { val: 8, label: "日期时间(yyyy-MM-dd HH:mm:ss)", name: "DateTimeWE" },
        { val: 9, label: "年份(yyyy)", name: "YearWE" },
        { val: 10, label: "年月(yyyy-MM)", name: "MonthWE" },
        { val: 11, label: "时间(HH:mm:ss)", name: "TimeWE" },
        { val: 14, label: "单选框", name: "RadioWE" },
        { val: 15, label: "多选框", name: "CheckboxWE" },
        { val: 16, label: "电子签名" },
      ],
      innerVisible: false,
      data: [],
      fieldsTableCopy: [],
      curFWEditor: "",
      curEditorType: "",
      showEditFieldDialogFlag: false,
      editingFieldName: "",
      savefields: {
        assignment_formula: null,
        associated_table_id: null,
        associated_table_name: null,
        associated_tablename_zh: null,
        associated_value: null,
        codename_allshow: null,
        data_code: null,
        dictortsql: null,
        facttable_id: "L2YHEL4X-1BFNL5TK-5GPZK2-6SXDL61-56DC1B69",
        field_length: 255,
        field_sql: null,
        file_path: null,
        file_size: null,
        image_path: null,
        is_edit: 1,
        is_mainfield: 0,
        is_null: 1,
        is_system: 0,
        label: "courseScore",
        name: "courseScore",
        num_length: null,
        optionList: null,
        order_number: 1,
        table_name: "cf_cs",
        verifiable: null,
      },
    };
  },
  methods: {
    getName(type) {
      let name = "";
      let obj = this.fieldType.find((item) => item.val == type);
      if (obj) {
        name = obj.label;
      }
      return name;
    },
    handleSave() {
      return this.fieldsTableCopy;
    },
    edit(command, row) {
      this.curFWEditor = command;
      this.curEditorType = command.replace(/WE$/, "");
      this.showEditFieldDialogFlag = true;
      this.editingFieldName = row.name;
      setTimeout(() => {
        this.$nextTick(() => {
          if (row.data_code) this.$refs.editField.getDtList(row.data_code);
          for (let i in this.savefields) {
            this.$refs.editField.fieldProps[i] = row[i];
          }
        });
      }, 500);
    },
    onFieldSaved(data) {
      let index = this.fieldsTableCopy.findIndex(
        (item) => item.name == data.name
      );
      if (index >= 0) {
        this.$set(this.fieldsTableCopy, index, data);
      }
      let index2 = this.fieldsTable.findIndex((item) => item.name == data.name);
      if (index2 >= 0) {
        this.$set(this.fieldsTable, index2, data);
      }
      this.showEditFieldDialogFlag = false;
    },

    onCancelSaveField() {
      this.showEditFieldDialogFlag = false;
    },
    async getTableData() {
      const { state, msg, t } = await importFields({
        resourcesName: this.selectedEntityObj.name,
        resourcesId: this.selectedEntityObj.id,
        targetName: this.pageInfo.value,
      });
      if (state == "error") {
        this.$message({
          type: state,
          message: msg,
        });
        return;
      }
      // this.importFieldsDialogFlag = true;
      this.fieldsTable = t;
      this.fieldsTableCopy = [...t];
    },
    handleDel(name, index) {
      this.fieldsTable.splice(index, 1);
      let copyIndex = this.fieldsTableCopy.findIndex(
        (item) => item.name == name
      );
      this.fieldsTableCopy.splice(copyIndex, 1);
    },
    getData() {
      this.data = this.fieldsTable.slice(
        (this.pageInfo.pageNum - 1) * this.pageInfo.pageSize,
        this.pageInfo.pageNum * this.pageInfo.pageSize
      );
    },
    filterData() {
      if (this.searchVal) {
        this.fieldsTable = this.fieldsTableCopy.filter(
          (item) =>
            item.name.toUpperCase().includes(this.searchVal.toUpperCase()) ||
            item.label.toUpperCase().includes(this.searchVal.toUpperCase())
        );
      } else {
        this.fieldsTable = [...this.fieldsTableCopy];
      }
    },
    handleCurrentChange(val) {
      this.pageInfo.pageNum = val;
      this.getData();
    },
    handleSizeChange(val) {
      this.pageInfo.pageSize = val;
      this.getData();
    },
  },
};
</script>
