<template>
  <div>
    <el-row :gutter="10">
      <el-col :span="12">
        <el-card class="box-card" shadow="hover">
          <div slot="header" class="clearfix">
            <span>流程</span>
            <el-button
              style="float: right; padding: 3px 0"
              type="text"
              size="mini"
              @click="handelAdd"
              >新增流程</el-button
            >
          </div>
          <el-table
            ref="processTable"
            :data="processList"
            size="mini"
            :height="200"
            highlight-current-row
            @current-change="getStep"
          >
            <el-table-column
              label="流程说明"
              align="center"
              prop="explain"
            ></el-table-column>
            <el-table-column label="启/停" align="center" prop="stateon">
              <template #default="{ row }">
                <el-switch
                  v-model="row.stateon"
                  active-color="#13ce66"
                  inactive-color="#ff4949"
                  :active-value="1"
                  :inactive-value="0"
                  @change="(val) => handelSave(val, 2, row)"
                >
                </el-switch>
              </template>
            </el-table-column>
            <el-table-column label="操作" align="center">
              <template #default="{ row }">
                <el-button size="mini" type="text" @click="handelEdit(row)"
                  >编辑</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </el-col>
      <el-col :span="12">
        <el-card class="box-card" shadow="hover">
          <div slot="header" class="clearfix">
            <span>步骤</span>
            <el-button
              style="float: right; padding: 3px 0"
              type="text"
              size="mini"
              @click="handelAddStep"
              >新增步骤</el-button
            >
          </div>
          <el-table
            ref="stepTable"
            :data="stepList"
            size="mini"
            :height="200"
            highlight-current-row
            @current-change="getBranch"
          >
            <el-table-column
              label="序号"
              align="center"
              prop="step_number"
            ></el-table-column>
            <el-table-column
              label="步骤说明"
              align="center"
              prop="step_explain"
            ></el-table-column>

            <el-table-column label="操作" align="center">
              <template #default="{ row, $index }">
                <el-button
                  size="mini"
                  type="text"
                  icon="el-icon-edit"
                  @click="handelEditStep(row)"
                ></el-button>
                <el-button
                  size="mini"
                  type="text"
                  icon="el-icon-top"
                  :disabled="$index == 0"
                  @click="changeNum(row, $index, -1)"
                ></el-button>
                <el-button
                  size="mini"
                  type="text"
                  icon="el-icon-bottom"
                  :disabled="$index == stepList.length - 1"
                  @click="changeNum(row, $index, 1)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </el-col>
    </el-row>
    <el-row :gutter="10" style="margin-top: 10px">
      <el-col :span="12">
        <el-card class="box-card" shadow="hover">
          <div slot="header" class="clearfix">
            <span>分支</span>
            <el-button
              style="float: right; padding: 3px 0"
              type="text"
              size="mini"
              @click="handelAddBranch"
              >新增分支</el-button
            >
          </div>
          <el-table
            ref="branchTable"
            :data="branchList"
            size="mini"
            :height="200"
            highlight-current-row
            @current-change="getUserRoles"
          >
            <el-table-column
              label="分支说明"
              prop="branch_explain"
              align="center"
            ></el-table-column>
            <el-table-column label="操作" align="center">
              <template #default="{ row }">
                <el-button
                  type="text"
                  size="mini"
                  @click="handelEditBranch(row)"
                  >编辑</el-button
                >
                <el-button type="text" size="mini" @click="handelDelBranch(row)"
                  >删除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </el-col>
      <el-col :span="12">
        <el-card class="box-card" shadow="hover">
          <div slot="header" class="clearfix">
            <span>权限</span>
            <el-popover
              ref="popover"
              placement="bottom"
              title="选择人员"
              width="300"
              trigger="click"
              style="float: right"
            >
              <el-table
                ref="multipleTable"
                :data="userList"
                :height="300"
                tooltip-effect="dark"
                size="mini"
                @selection-change="handleSelectionChange"
              >
                <el-table-column>
                  <template slot="header" slot-scope="scope">
                    <el-input
                      :key="scope.$index"
                      v-model="name"
                      @input="getUsers"
                    ></el-input>
                  </template>
                  <el-table-column type="selection" width="55" align="center">
                  </el-table-column>
                  <el-table-column
                    label="姓名"
                    prop="name"
                    align="center"
                  ></el-table-column>
                  <el-table-column
                    label="工号"
                    prop="code"
                    align="center"
                  ></el-table-column>
                </el-table-column>
              </el-table>
              <div style="text-align: right; margin-top: 20px">
                <el-button size="mini" @click="$refs.popover.doClose()"
                  >取 消</el-button
                >
                <el-button size="mini" type="primary" @click="handelSaveUser"
                  >确 定</el-button
                >
              </div>
              <el-button
                slot="reference"
                type="text"
                size="mini"
                @click="getUsers('')"
                >新增权限</el-button
              >
            </el-popover>
          </div>
          <el-scrollbar style="height: 200px">
            <el-tag
              v-for="(item, index) in branchCurrentRow.toexamine_names
                ? branchCurrentRow.toexamine_names.split(',')
                : []"
              :key="index"
              style="margin-right: 10px"
            >
              {{ item }}
            </el-tag>
          </el-scrollbar>
        </el-card>
      </el-col>
    </el-row>

    <el-dialog
      title="流程"
      :visible.sync="designVisible"
      width="30%"
      :before-close="handleClose"
      destroy-on-close
      append-to-body
    >
      <el-form :model="form" label-width="80px">
        <el-form-item label="流程说明">
          <el-input v-model="form.process_explain"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="designVisible = false">取 消</el-button>
        <el-button type="primary" @click="handelSave">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="步骤"
      :visible.sync="stepVisible"
      width="30%"
      :before-close="handleClose"
      destroy-on-close
      append-to-body
    >
      <el-form :model="step" label-width="80px">
        <el-form-item label="序号">
          <el-input v-model="step.step_number" disabled></el-input>
        </el-form-item>
        <el-form-item label="步骤说明">
          <el-input v-model="step.step_explain"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="stepVisible = false">取 消</el-button>
        <el-button type="primary" @click="handelSaveStep">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="分支"
      :visible.sync="branchVisible"
      width="30%"
      :before-close="handleClose"
      destroy-on-close
      append-to-body
    >
      <el-form :model="branch" label-width="80px">
        <el-form-item label="分支说明">
          <el-input v-model="branch.branch_explain"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="branchVisible = false">取 消</el-button>
        <el-button type="primary" @click="handelSaveBranch">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {
  getProcess,
  saveProcess,
  getStepByProcessCode,
  saveStep,
  getBranchByStepCode,
  setStepNumber,
  saveBranch,
  delBranch,
  findAllUser,
  setUsers,
} from "@/api";
export default {
  data() {
    return {
      processList: [],
      stepVisible: false,
      form: {
        process_explain: "",
        form_id: "",
        stateon: 0,
        process_code: "",
        step_number: 1,
        step_explain: "步骤一",
        branch_explain: "分支一",
      },
      designVisible: false,
      selectedEntityObj: {},
      stepList: [],
      step: {
        process_explain: "",
        form_id: "",
        stateon: 0,
        process_code: "",
        step_number: "",
        step_explain: "",
        branch_explain: "分支一",
      },
      branchList: [],
      branchVisible: false,
      branch: {
        process_explain: "",
        form_id: "",
        stateon: 0,
        process_code: "",
        step_number: "",
        step_explain: "",
        branch_explain: "",
      },
      stepCurrentRow: {},
      processCurrentRow: {},
      branchCurrentRow: {},
      userList: [],
      name: "",
      selection: [],
    };
  },
  methods: {
    async handelSaveUser() {
      if (!this.branchCurrentRow.id)
        return this.$message({ message: "请选择分支", type: "error" });
      const { state, msg } = await setUsers({
        toexamine_names: this.selection
          .map((item) => {
            return item.name;
          })
          .join(","),
        toexamine_users: this.selection
          .map((item) => {
            return item.code;
          })
          .join(","),
        id: this.branchCurrentRow.id,
      });
      this.$refs.popover.doClose()
      this.$message({ message: msg, type: state });
      this.getBranch(this.branchCurrentRow);
    },
    getUserRoles(row) {
      this.branchCurrentRow = row;
    },
    handleSelectionChange(val) {
      this.selection = val;
    },
    async getUsers(name) {
      const { t } = await findAllUser({ name });
      this.userList = t;
      let users = this.branchCurrentRow.toexamine_users
        ? this.branchCurrentRow.toexamine_users.split(",")
        : [];
      let userRoles = this.userList.filter((item) => {
        return users.includes(item.code);
      });
      this.$nextTick(() => {
        userRoles.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row, true);
        });
      });
    },
    async changeNum(row, index, i) {
      const { state, msg } = await setStepNumber({
        formId: this.selectedEntityObj.id,
        processCode: this.step.process_code,
        stepNumberNew: row.step_number + i,
        stepNumberOld: row.step_number,
      });
      this.$message({ message: msg, type: state });
      if (state == "success") {
        this.getStep({
          explain: this.step.process_explain,
          stateon: this.step.stateon,
          code: this.step.process_code,
        });
      }
    },
    handelAdd() {
      this.designVisible = true;
      this.form.process_explain = "";
    },
    handelAddBranch() {
      this.branchVisible = true;
      this.branch.branch_explain = "";
      this.branch.id = null;
    },
    handelAddStep() {
      this.step.step_number = this.stepList.length
        ? this.stepList.length + 1
        : 1;
      this.step.step_explain = "";
      this.stepVisible = true;
    },
    async findProcess(selectedEntityObj) {
      this.selectedEntityObj = selectedEntityObj;
      const { t } = await getProcess({ formId: selectedEntityObj.id });
      this.processList = t ? t : [];
      if (this.processList && this.processList.length > 0) {
        const index = this.processList.findIndex((value, index, arr) => {
          return (
            value.code == this.processCurrentRow.code &&
            value.explain == this.processCurrentRow.explain &&
            value.stateon == this.processCurrentRow.stateon &&
            value.stepId == this.processCurrentRow.stepId
          );
        });
        this.$refs.processTable.setCurrentRow(
          this.processList[index > 0 ? index : 0]
        );
      } else {
        this.stepList = [];
        this.branchList = [];
      }
    },
    handelEdit(row) {
      this.form = {
        process_explain: row.explain,
        form_id: this.selectedEntityObj.id,
        stateon: row.stateon,
        process_code: row.code,
      };
      this.designVisible = true;
    },
    async handelSave(val, i, row) {
      let postData;
      if (i == 2) {
        postData = {
          process_explain: row.explain,
          form_id: this.selectedEntityObj.id,
          stateon: val,
          process_code: row.code,
          step_explain: "步骤一",
          branch_explain: "分支一",
        };
      } else {
        postData = this.form;
        postData.form_id = this.selectedEntityObj.id;
      }

      const { state, msg } = await saveProcess(postData);
      this.$message({ message: msg, type: state });
      this.designVisible = false;
      this.findProcess(this.selectedEntityObj);
    },
    async getStep(row) {
      if (!row) return;
      this.processCurrentRow = row;
      this.step.process_explain = row.explain;
      this.step.form_id = this.selectedEntityObj.id;
      this.step.stateon = row.stateon;
      this.step.process_code = row.code;
      const { t } = await getStepByProcessCode({
        formId: this.selectedEntityObj.id,
        processcode: row.code,
      });
      this.stepList = t ? t : [];
      if (this.stepList && this.stepList.length > 0) {
        const index = this.stepList.findIndex((value, index, arr) => {
          return (
            value.form_id == this.stepCurrentRow.form_id &&
            value.id == this.stepCurrentRow.id &&
            value.process_code == this.stepCurrentRow.process_code &&
            value.step_explain == this.stepCurrentRow.step_explain &&
            value.step_number == this.stepCurrentRow.step_number
          );
        });
        this.$refs.stepTable.setCurrentRow(
          this.stepList[index > 0 ? index : 0]
        );
      } else {
        this.branchList = [];
      }
    },
    handelEditStep(row) {
      this.step = { ...row };
      this.stepVisible = true;
    },
    async handelSaveStep() {
      if (!this.step.form_id)
        return this.$message({ message: "请选中流程", type: "error" });
      const { state, msg } = await saveStep(this.step);
      this.$message({ message: msg, type: state });
      this.stepVisible = false;
      this.getStep({
        explain: this.step.process_explain,
        stateon: this.step.stateon,
        code: this.step.process_code,
      });
    },
    async getBranch(row) {
      if (!row) return;
      this.stepCurrentRow = { ...row };
      const { t } = await getBranchByStepCode({
        formId: this.selectedEntityObj.id,
        stepNumber: row.step_number,
        processCode: row.process_code,
      });
      this.branchList = t ? t : [];
      if (this.branchList && this.branchList.length > 0) {
        const index = this.branchList.findIndex((value, index, arr) => {
          return (
            value.code == this.branchCurrentRow.code &&
            value.explain == this.branchCurrentRow.explain &&
            value.stateon == this.branchCurrentRow.stateon &&
            value.stepId == this.branchCurrentRow.stepId &&
            value.branch_explain == this.branchCurrentRow.branch_explain &&
            value.id == this.branchCurrentRow.id
          );
        });
          this.$refs.branchTable.setCurrentRow(
            this.branchList[index > 0 ? index : 0]
          );

      }
    },
    handelEditBranch(row) {
      this.branch = { ...row };
      this.branchVisible = true;
    },
    async handelSaveBranch() {
      let postData = {
        process_explain: this.stepCurrentRow.process_explain,
        form_id: this.stepCurrentRow.form_id,
        stateon: this.stepCurrentRow.stateon,
        process_code: this.stepCurrentRow.process_code,
        step_number: this.stepCurrentRow.step_number,
        step_explain: this.stepCurrentRow.step_explain,
        branch_explain: this.branch.branch_explain,
        id: this.branch.id ? this.branch.id : null,
      };
      const { state, msg } = await saveBranch(postData);
      this.$message({ message: msg, type: state });
      this.getBranch(this.stepCurrentRow);
      this.branchVisible = false;
    },
    handelDelBranch(row) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(async () => {
        const { state, msg } = await delBranch({
          formId: this.selectedEntityObj.id,
          id: row.id,
          processCode: row.process_code,
          stepNumber: row.step_number,
        });
        this.$message({
          type: state,
          message: msg,
        });
        if (state == "success") {
          this.findProcess(this.selectedEntityObj);
        }
      });
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(() => {
          done();
        })
        .catch(() => {});
    },
  },
};
</script>

<style scoped></style>
