<template>
  <el-container>
    <el-header class="entity-action-section"> 实体列表 </el-header>
    <el-main class="card-container">
      <el-input
        v-model="queryForm.val"
        class="search"
        @keyup.native.enter="getEntityList()"
      >
        <i slot="prefix" class="el-input__icon el-icon-search"></i>
      </el-input>
      <el-card class="entity-card" shadow="hover">
        <div>
          <el-button
            type="text"
            :disabled="!queryForm.classify_id"
            @click="createNewEntity"
          >
            <i class="el-icon-plus" style="font-size: 30px"></i>
          </el-button>
        </div>

        <el-upload
          class="upload-demo"
          :action="url + '/dev/transplantModel/importZipFile'"
          name="multipartFile"
          :data="{ classifyid: queryForm.classify_id }"
          :multiple="false"
          :limit="1"
          :show-file-list="false"
          :on-success="handleSuccess"
          :on-error="handleError"
          :disabled="!queryForm.classify_id"
        >
          <el-button
            icon="el-icon-upload"
            size="mini"
            plain
            type="primary"
            :disabled="!queryForm.classify_id"
            >导入配置</el-button
          >
        </el-upload>
      </el-card>
      <el-card
        v-for="(entityItem, entityIdx) in entityItems"
        :key="entityIdx"
        class="entity-card"
        shadow="hover"
        @click.native.stop="(event) => showContextMenu(entityItem, event)"
        @contextmenu.native.prevent.stop="
          (event) => showContextMenu(entityItem, event)
        "
        @mouseenter.native="
          onEnterEntity(entityItem.name, entityItem.label, entityIdx)
        "
        @mouseleave.native="onLeaveEntity"
      >
        <div slot="header">
          <span>{{ entityItem.label }}</span>
        </div>
        <div>{{ entityItem.name }}</div>
        <!-- <div
          v-if="!!entityItem.systemEntityFlag"
          class="system-flag system-entity"
        >
          <i title="系统实体">系</i>
        </div>-->
        <div
          v-if="entityItem.is_childtable == 0"
          class="entity-flag main-entity"
        >
          <i title="主实体">主</i>
        </div>
        <div
          v-if="entityItem.is_childtable == 2"
          class="entity-flag detail-entity"
        >
          <el-tooltip
            class="item"
            effect="dark"
            content="一对多"
            placement="top-start"
          >
            <i>从</i>
          </el-tooltip>
        </div>
        <div
          v-if="entityItem.is_childtable == 1"
          class="entity-flag detail-entity"
        >
          <el-tooltip
            class="item"
            effect="dark"
            content="一对一"
            placement="top-start"
          >
            <i>从</i>
          </el-tooltip>
        </div>
      </el-card>

      <el-dialog
        v-if="showNewEntityDialogFlag"
        title="新建实体"
        :visible.sync="showNewEntityDialogFlag"
        :show-close="false"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
      >
        <EntityPropEditor
          ref="EPEditor"
          :entity-props="newEntityProps"
          :show-title="false"
          :filter-entity-method="filterMainEntity"
        ></EntityPropEditor>
        <div slot="footer" class="dialog-footer">
          <el-button
            type="primary"
            size="small"
            style="width: 90px"
            @click="saveNewEntity"
          >
            保 存
          </el-button>
          <el-button size="small" @click="showNewEntityDialogFlag = false">
            取 消
          </el-button>
        </div>
      </el-dialog>

      <div
        v-show="contextMenuVisible"
        id="contextMenu"
        class="context-menu"
        @mouseenter="clearHideMenuTimer"
        @mouseleave="setHideMenuTimer"
      >
        <div class="context-menu__item" @click.stop>
          模型管理
          <i
            class="el-icon-arrow-right"
            style="position: absolute; right: 5px; top: 10px"
          ></i>
          <div class="context-menu" icon="el-icon-arrow-right">
            <div
              class="context-menu__item"
              @click="editSelectedEntity(selectedEntityObj)"
            >
              修改实体
            </div>
            <div
              class="context-menu__item"
              @click="deleteSelectedEntity(selectedEntityObj)"
            >
              删除实体
            </div>
            <div
              v-if="selectedEntityObj.is_childtable == 0"
              class="context-menu__item"
              @click="gotoExport(selectedEntityObj)"
            >
              导出配置
            </div>
          </div>
        </div>
        <div class="context-menu__item" @click.stop>
          字段管理<i
            class="el-icon-arrow-right"
            style="position: absolute; right: 5px; top: 10px"
          ></i>
          <div class="context-menu">
            <div
              class="context-menu__item"
              @click="gotoEntityManager(selectedEntityObj)"
            >
              字段维护
            </div>
            <div
              class="context-menu__item"
              @click="importFields(selectedEntityObj)"
            >
              导入字段
            </div>
          </div>
        </div>

        <div
          v-if="selectedEntityObj.is_childtable != 2"
          class="context-menu__item"
          @click="gotoFormLayout(selectedEntityObj)"
        >
          表单设计
        </div>
        <div
          v-if="selectedEntityObj.is_childtable != 1"
          class="context-menu__item"
          @click="gotoListSetting(selectedEntityObj)"
        >
          列表设计
        </div>
        <div
          v-if="selectedEntityObj.is_childtable == 0"
          class="context-menu__item"
          @click="processDesign(selectedEntityObj)"
        >
          流程设计
        </div>
        <div
          v-if="selectedEntityObj.is_childtable == 0"
          class="context-menu__item"
          @click="gotoListShow(selectedEntityObj)"
        >
          预览
        </div>

        <div
          v-if="selectedEntityObj.is_childtable == 0"
          class="context-menu__item"
          @click="onCopy(selectedEntityObj)"
        >
          复制链接
        </div>
      </div>
    </el-main>
    <el-dialog
      title="字段管理"
      :visible.sync="dialogVisible"
      width="80%"
      :before-close="handleClose"
    >
      <EntityFieldTable ref="EntityFieldTable" :entity="selectedEntityObj" />
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false">
          关 闭
        </el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="表单设计"
      :visible.sync="dialogVisible2"
      width="100%"
      :before-close="handleClose"
      fullscreen
      class="bdsj"
    >
      <layout ref="layout" :entity="selectedEntityObj" />

      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible2 = false">
          关 闭
        </el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="列表设计"
      :visible.sync="dialogVisible3"
      width="100%"
      :before-close="handleClose"
      fullscreen
      class="bdsj"
    >
      <listSetting
        ref="listSetting"
        @close="dialogVisible3 = false"
      ></listSetting>

      <!--      <span slot="footer" class="dialog-footer">-->
      <!--        <el-button type="primary" @click="dialogVisible3 = false">-->
      <!--          关 闭-->
      <!--        </el-button>-->
      <!--      </span>-->
    </el-dialog>
    <el-dialog
      title="导入字段"
      :visible.sync="importFieldsDialogFlag"
      width="60%"
      :before-close="handleClose"
      destroy-on-close
    >
      <FieldsTable ref="FieldsTable" />
      <span slot="footer" class="dialog-footer">
        <el-button @click="importFieldsDialogFlag = false">取 消</el-button>
        <el-button type="primary" @click="handleSave">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="流程设计"
      :visible.sync="designVisible"
      width="80%"
      :before-close="handleClose"
      destroy-on-close
    >
      <designProcess ref="designProcess"></designProcess>
      <span slot="footer" class="dialog-footer">
        <el-button @click="designVisible = false">取 消</el-button>
        <el-button type="primary" @click="designVisible = false"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </el-container>
</template>

<script>
import {
  getTableList,
  saveFactTable,
  delFactTable,
  truncateTable,
  importFieldsSave,
} from "@/api";
import EntityPropEditor from "./entity-editor/entity-property-editor";
import EntityFieldTable from "@/views/system/entity-field-table";
import FieldsTable from "./FieldsTable";
import layout from "./form-layout";
import listSetting from "./listSetting/index";
import designProcess from "./designProcess";
import { baseURL } from "@/config";
export default {
  name: "EntityList",
  components: {
    EntityPropEditor,
    EntityFieldTable,
    layout,
    listSetting,
    FieldsTable,
    designProcess,
  },
  props: ["classify_id"],
  data() {
    return {
      url: baseURL,
      dialogVisible: false,
      dialogVisible2: false,
      dialogVisible3: false,
      designVisible: false,
      entityItems: [],
      showNewEntityDialogFlag: false,
      importFieldsDialogFlag: false,
      newEntityProps: {
        name: "",
        label: "",
        is_childtable: 0,
        is_reviewed: 1,
        classify_id: "",
        order_number: null,
        onable_role: 0,
        // id: null,
        // entityCode: null,
        // layoutable: true,
        // listable: true,
        // authorizable: true,
        // assignable: false,
        // shareable: false,
        // mainEntity: "",
        // detailEntityFlag: false,
      },

      hoverEntityIdx: -1,

      selectedEntityObj: {},
      contextMenuVisible: false,
      hideMenuTimerId: null,
      queryForm: {
        classify_id: "",
        val: "",
      },
    };
  },
  computed: {},
  mounted() {
    document.addEventListener("click", (e) => {
      this.hideContextMenu();
    });
  },
  methods: {
    async handleSave() {
      let data = this.$refs.FieldsTable.handleSave();
      const { state, msg } = await importFieldsSave(data);
      this.$message({ type: state, message: msg });
      this.importFieldsDialogFlag = false;
    },
    handleSuccess(response, file, fileList) {
      const { t, state, msg } = response;
      this.$message({ type: state, message: msg });
      if (state == "success") {
        this.getEntityList();
      }
    },
    handleError(err, file, fileList) {
      this.$message.error("文件上传失败！");
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(() => {
          done();
        })
        .catch(() => {});
    },
    async getEntityList() {
      const { t, state, msg } = await getTableList(this.queryForm);
      if (state == "error") {
        this.$message({ message: msg, type: "error" });
        return;
      }
      this.entityItems = t;
    },
    editSelectedEntity(selectedEntityObj) {
      this.newEntityProps = { ...selectedEntityObj };
      this.showNewEntityDialogFlag = true;
    },
    gotoExport(selectedEntityObj) {
      const a = document.createElement("a");
      a.target = "_blank";
      a.href = `${baseURL}/dev/transplantModel/exportZip?table_id=${selectedEntityObj.id}&table_name=${selectedEntityObj.name}`;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
    },
    importFields(selectedEntityObj) {
      this.$prompt("请输入表名", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        // inputPattern: /(^\s*)|(\s*$)/g,
        inputErrorMessage: "表名不能为空",
        inputValidator: (value) => {
          if (!value) {
            return "表名不能为空";
          }
        },
      }).then(({ value }) => {
        this.importFieldsDialogFlag = true;
        this.$nextTick(() => {
          this.$refs.FieldsTable.pageInfo.value = value;
          this.$refs.FieldsTable.selectedEntityObj = selectedEntityObj;
          this.$refs.FieldsTable.getTableData();
        });
      });
      // this.importFieldsDialogFlag = true;
    },

    createNewEntity() {
      this.newEntityProps = this.$options.data().newEntityProps;
      this.newEntityProps.classify_id = this.queryForm.classify_id;
      if (this.entityItems.length > 0) {
        this.newEntityProps.order_number =
          this.entityItems[this.entityItems.length - 1].order_number + 1;
      } else {
        this.newEntityProps.order_number = 1;
      }

      this.showNewEntityDialogFlag = true;
    },

    async saveNewEntity() {
      if (!this.$refs["EPEditor"].validateForm()) return;
      if (!this.newEntityProps.id) {
        this.newEntityProps.name = "cf_" + this.newEntityProps.name;
      }
      const { state, msg } = await saveFactTable(this.newEntityProps);
      if (state == "error") {
        this.$message({ message: msg, type: "error" });
        return;
      }
      this.$message.success("保存成功");
      this.showNewEntityDialogFlag = false;
      this.getEntityList();
    },

    onEnterEntity(entityName, entityLabel, entityIdx) {
      this.hoverEntityIdx = entityIdx;
    },

    onLeaveEntity() {
      this.hoverEntityIdx = -1;
    },

    gotoEntityManager(selectedEntityObj) {
      this.selectedEntityObj = selectedEntityObj;
      this.dialogVisible = true;
      this.$nextTick(() => {
        this.$refs.EntityFieldTable.entityProps =
          this.$refs.EntityFieldTable.entity = { ...this.selectedEntityObj };
        this.$refs.EntityFieldTable.entityCopy = {
          ...this.selectedEntityObj,
        };

        this.$refs.EntityFieldTable.resizeTableHeight();

        this.$refs.EntityFieldTable.initPageData();
        this.$refs.EntityFieldTable.entityItems = this.entityItems;
      });
    },

    gotoFormLayout() {
      // if (selectedEntityObj.layoutable !== true) {
      //   this.$message.info("当前实体不允许设计表单");
      //   return;
      // }
      this.dialogVisible2 = true;
      this.$nextTick(() => {
        this.$refs.layout.entity = this.selectedEntityObj;
        this.$refs.layout.entityItems = this.entityItems;

        this.$refs.layout.getEntityFields(true, () => {
          this.$refs.layout.initFormLayout(true);
        });
      });

      // this.$router.push({
      //   name: "FormLayout",
      //   params: {
      //     entity: selectedEntityObj.name,
      //     entityLabel: selectedEntityObj.label,
      //   },
      // });
    },
    gotoListSetting(selectedEntityObj) {
      this.dialogVisible3 = true;
      this.$nextTick(() => {
        this.$refs.listSetting.entity = selectedEntityObj;
        this.$refs.listSetting.resizeTableHeight();
        this.$refs.listSetting.getList();
      });
    },
    gotoListShow(selectedEntityObj) {
      this.$router.push({
        name: "ListSetting",
        params: {
          entity: selectedEntityObj,
        },
      });
    },
    onCopy(selectedEntityObj) {
      this.$copyText(selectedEntityObj.id + ";" + selectedEntityObj.name)
        .then(() => {
          this.$message.success({ message: "已成功复制到剪切板" });
        })
        .catch(() => {
          this.$message.error({ message: "复制失败" });
        });
    },
    //流程设计
    processDesign(selectedEntityObj) {
      this.designVisible = true;
      this.$nextTick(() => {
        this.$refs.designProcess.findProcess(selectedEntityObj);
      });
    },
    deleteSelectedEntity(selectedEntityObj) {
      let confirmText = [
        "实体删除后不能恢复，是否确认删除?",
        "1. 删除实体会清空该实体的所有数据，包括实体所有字段、表单布局和列表设置，且不能恢复；’",
        "2. 如该实体包含明细实体，请先删除明细实体；",
        "3. 如有其他实体引用字段指向该实体，请手工删除引用字段；",
      ];
      const h = this.$createElement;
      let pTags = [];
      confirmText.forEach((ct) => {
        pTags.push(h("p", null, ct));
      });
      this.$confirm("删除提醒", {
        message: h("div", null, pTags),
        showCancelButton: true,
        type: "warning",
      })
        .then(() => {
          delFactTable({
            id: selectedEntityObj.id,
            name: selectedEntityObj.name,
            is_childtable: this.selectedEntityObj.is_childtable,
          })
            .then((res) => {
              if (res.state == "error") {
                if (res.t == 1) {
                  const m = this.$createElement;
                  this.$confirm("警告", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    message: h("p", null, [
                      h(
                        "i",
                        { style: "color: red" },
                        `${res.msg},此操作会清空表数据，是否继续?`
                      ),
                    ]),
                    type: "error",
                  }).then(() => {
                    truncateTable({ name: selectedEntityObj.name })
                      .then((res2) => {
                        if (res2.state == "error") {
                          this.$message({ message: res2.msg, type: "error" });
                          return;
                        } else {
                          this.$message({ message: res2.msg, type: "success" });
                          this.getEntityList();
                        }
                      })
                      .catch((res2) => {
                        this.$message({ message: res2.message, type: "error" });
                      });
                  });
                } else {
                  this.$message({ message: res.msg, type: "error" });
                }
                return;
              }
              this.$message({ message: "实体已删除", type: "success" });
              this.getEntityList();
            })
            .catch((res) => {
              this.$message({ message: res.message, type: "error" });
            });
        })
        .catch(() => {
          this.$message({ type: "info", message: "已取消删除" });
        });
    },

    // executeDeleteEntity(entity) {
    //   deleteEntity(entity)
    //     .then((res) => {
    //       if (res.error != null) {
    //         this.$message({ message: res.error, type: "error" });
    //         return;
    //       }

    //       this.$message.success("实体已删除");
    //       this.getEntityList();
    //     })
    //     .catch((res) => {
    //       this.$message({ message: res.message, type: "error" });
    //     });
    // },

    filterMainEntity(filterList, callBack) {
      filterList.length = 0; /* 清空数组，不能用filterList=[]，否则SimpleTable显示不出数据！！ */
      this.entityItems.filter((entity) => {
        if (entity.is_childtable === 0) {
          filterList.push({
            name: entity.name,
            label: entity.label,
            id: entity.id,
          });
        }
      });
      callBack();
    },

    hideContextMenu() {
      this.contextMenuVisible = false;
      this.clearHideMenuTimer();
    },

    contextMenuSetting(menu, event) {
      if (event.clientX > 1800) {
        menu.style.left = event.clientX - 100 + "px";
      } else {
        menu.style.left = event.clientX - 200 + "px";
      }
      if (event.clientY > 700) {
        menu.style.top = event.clientY - 30 + "px";
      } else {
        menu.style.top = event.clientY - 10 + "px";
      }
    },

    showContextMenu(entity, event) {
      this.clearHideMenuTimer();
      this.contextMenuVisible = false;
      this.contextMenuVisible = true;
      //event.preventDefault() //关闭浏览器右键默认事件
      // this.selectedEntityObj = {
      //   name: entity.name,
      //   label: entity.label,
      //   layoutable: entity.layoutable,
      //   listable: entity.listable,
      //   systemEntityFlag: entity.systemEntityFlag,
      // };
      this.selectedEntityObj = entity;
      let menu = document.querySelector("#contextMenu");
      this.contextMenuSetting(menu, event);
      this.setHideMenuTimer();
    },

    setHideMenuTimer() {
      this.hideMenuTimerId = setTimeout(() => {
        this.hideContextMenu();
      }, 3000);
    },

    clearHideMenuTimer() {
      clearTimeout(this.hideMenuTimerId);
    },
  },
};
</script>

