<template>
  <el-container class="entity-props-container">
    <el-header v-if="showTitle" class="entity-props-header">
      &lt;实体&gt;属性设置
    </el-header>
    <el-main class="entity-props-pane">
      <el-form
        ref="entityPropsForm"
        :model="entityProps"
        :rules="rules"
        label-position="left"
        label-width="360px"
        size="mini"
      >
        <el-form-item label="序号" prop="order_number">
          <el-input-number
            v-model="entityProps.order_number"
            :min="0"
          ></el-input-number>
        </el-form-item>
        <el-form-item label="实体名称" prop="name">
          <span slot="label"
            >实体名称（<span style="color: red">* 表名前会自动添加cf_</span
            >）</span
          >
          <!-- prop必须跟v-model名称一致！！ -->
          <el-input
            v-model="entityProps.name"
            minlength="2"
            :disabled="!!entityProps.id"
          ></el-input>
        </el-form-item>
        <el-form-item label="显示名称" prop="label">
          <el-input v-model="entityProps.label" minlength="2"></el-input>
        </el-form-item>
        <!-- <el-form-item label="实体编码">
          <el-input
            v-model="entityProps.entityCode"
            readonly
            disabled
          ></el-input>
        </el-form-item> -->
        <!-- <el-form-item label="是否允许设计表单">
          <el-switch
            style="display: block; float: right"
            v-model="entityProps.layoutable"
            active-text="是"
            inactive-text="否"
          ></el-switch>
        </el-form-item>
        <el-form-item label="是否允许设计列表">
          <el-switch
            style="display: block; float: right"
            v-model="entityProps.listable"
            active-text="是"
            inactive-text="否"
          ></el-switch>
        </el-form-item>-->
        <el-form-item label="是否从表">
          <el-radio-group
            v-model="entityProps.is_childtable"
            :disabled="!!entityProps.id"
            @change="clearMainEntity"
          >
            <el-radio :label="0">否</el-radio>
            <el-radio :label="1">一对一</el-radio>
            <el-radio :label="2">一对多</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="所属主实体">
          <el-input
            v-model="entityProps.maintable_name"
            readonly
            :disabled="entityProps.is_childtable == 0"
          >
            <el-button
              v-if="entityProps.is_childtable != 0"
              slot="append"
              icon="el-icon-close"
              title="清除"
              @click="clearMainEntity"
            ></el-button>
            <el-button
              v-if="entityProps.is_childtable != 0"
              slot="append"
              icon="el-icon-search"
              title="选择主实体"
              @click="showEntityListDialog"
            ></el-button>
          </el-input>
        </el-form-item>
        <el-form-item
          v-if="entityProps.is_childtable == 0"
          label="是否开启数据权限"
        >
          <el-radio-group v-model="entityProps.onable_role">
            <el-radio :label="0">否</el-radio>
            <el-radio :label="1">科室权限</el-radio>
            <el-radio :label="2">人员权限</el-radio>
          </el-radio-group>
        </el-form-item>
        <!--
        <el-form-item label="是否允许分配记录">
          <el-switch style="display: block; float: right" v-model="entityProps.assignable"
                     active-text="开启" inactive-text="禁用" :disabled="!!!entityProps.authorizable">
          </el-switch>
        </el-form-item>
        <el-form-item label="是否允许共享记录">
          <el-switch style="display: block; float: right" v-model="entityProps.shareable"
                     active-text="开启" inactive-text="禁用" :disabled="!!!entityProps.authorizable">
          </el-switch>
        </el-form-item>
        -->
        <el-form-item v-if="entityProps.is_childtable == 0" label="是否审核">
          <el-switch
            v-model="entityProps.is_reviewed"
            active-text="是"
            inactive-text="否"
            :active-value="1"
            :inactive-value="0"
          ></el-switch>
        </el-form-item>
      </el-form>
    </el-main>

    <el-dialog
      ref="selectMainEntityDlg"
      title="选择主实体"
      :visible.sync="showMainEntityDialogFlag"
      :append-to-body="true"
      custom-class="no-padding"
      width="560px"
    >
      <SimpleTable
        ref="SimpleTable"
        :show-pagination="false"
        :show-check-box="false"
        :table-size="'mini'"
        :columns="columns"
        :data="tableData"
        :max-height="420"
      >
        <el-table-column
          slot="table_operation"
          align="center"
          label="操作"
          width="150"
          :resizable="false"
        >
          <template slot-scope="scope">
            <el-button
              class=""
              icon="el-icon-check"
              size="mini"
              @click="selectMainEntity(scope.row)"
            >
              选择
            </el-button>
          </template>
        </el-table-column>
      </SimpleTable>
    </el-dialog>
  </el-container>
</template>

<script>
export default {
  name: "EntityPropEditor",
  props: ["entityProps", "showTitle", "filterEntityMethod"],
  data() {
    return {
      rules: {
        order_number: [
          { required: true, message: "请输入序号", trigger: "blur" },
        ],
        name: [
          { required: true, message: "请输入实体名称", trigger: "blur" },
          // {
          //   pattern: /^[A-Z]+[A-Za-z\d_-]*$/,
          //   message:
          //     "请以英文大写字母开头，不可包含中文，中间可输入字母、下划线或横杠",
          //   trigger: "blur",
          // },
          { min: 2, max: 30, message: "请输入至少两个字符", trigger: "blur" },
        ],
        label: [
          { required: true, message: "请输入显示名称", trigger: "blur" },
          {
            pattern: /^[A-Za-z\d\u4e00-\u9fa5]+[_-]*/,
            message: "请以中文、英文字母、数字开头，中间可输入下划线或横杠",
            trigger: "blur",
          },
          { min: 2, max: 30, message: "请输入至少两个字符", trigger: "blur" },
        ],
      },

      showMainEntityDialogFlag: false,
      mainEntityName: "",

      columns: [
        { prop: "name", label: "实体名称", width: "150", align: "center" },
        {
          prop: "label",
          label: "显示名称",
          width: "200",
          align: "center",
          // formatter: this.formatter,
        },
      ],
      tableData: [],
      onable_role: "",
    };
  },

  methods: {
    onToggleDetailEntityFlag(val) {
      if (val == 0) {
        this.entityProps.detailEntityFlag = true;
      }
      if (val == 1) {
        this.entityProps.detailEntityFlag = false;
      }
    },

    showEntityListDialog() {
      if (this.filterEntityMethod) {
        let data = this.filterEntityMethod(this.tableData, () => {
          this.showMainEntityDialogFlag = true;
          this.$nextTick(() => {
            console.log(this.tableData);
            this.$refs.SimpleTable.updatable(this.tableData, this.columns);
          });
        });
      }
    },

    selectMainEntity(row) {
      // console.log(row)
      if (row) {
        console.log(row);
        this.entityProps.maintable_name = row.name;
        this.entityProps.maintable_id = row.id;
        // this.mainEntityName = row.label + "(" + row.name + ")";
        this.showMainEntityDialogFlag = false;
      }
    },

    clearMainEntity() {
      // this.mainEntityName = "";
      this.entityProps.maintable_name = "";
      this.entityProps.maintable_id = "";
    },

    changeAuthorization(value) {
      if (!value) {
        this.entityProps.assignable = false;
        this.entityProps.shareable = false;
      } else {
        this.entityProps.detailEntityFlag = false;
      }
    },

    validateForm() {
      let validResult = true;
      this.$refs["entityPropsForm"].validate((success) => {
        if (!success) {
          validResult = false;
          return false;
        }
      });

      if (
        this.entityProps.is_childtable &&
        !this.entityProps.maintable_name
      ) {
        this.$message.info("请选择所属主实体");
        return false;
      }

      return validResult;
    },
  },
};
</script>

