<template>
  <div style="display: flex; height: 100%">
    <div style="width: 160px; display: inline-block">
      <div class="logo">表单设计器</div>
      <el-scrollbar
        style="
          height: calc(100% - 60px);
          width: 160px;
          border-right: solid 1px #e6e6e6;
        "
      >

        <ul class="list">
          <li style="justify-content: center;height: 40px">
            <el-button
              type="text"
              icon="el-icon-plus"
              size="mini"
              style="font-size: 18px"
              @click="handleAdd"
            >
            </el-button>
          </li>
          <li
            v-for="item in list"
            :key="item.id"
            :class="active == item.id ? 'active' : ''"
            @click="getTable(item)"
          >
            <el-button type="text">
              {{ item.name }}
            </el-button>

            <el-dropdown
              v-show="active == item.id"
              @command="(val) => handleEdit(item, val)"
            >
              <span class="el-dropdown-link">
                <i class="el-icon-setting"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="a">修改</el-dropdown-item>
                <el-dropdown-item divided command="b">删除</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </li>
        </ul>
      </el-scrollbar>
    </div>
    <el-scrollbar class="right">
      <entityList ref="entityList" />
    </el-scrollbar>
    <el-dialog
      :title="`${classifyEntity.id ? '修改' : '新增'}主题`"
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose"
    >
      <el-form
        ref="classifyEntity"
        :model="classifyEntity"
        :rules="rules"
        label-width="80px"
      >
        <el-form-item label="主题名称" prop="name">
          <el-input v-model.trim="classifyEntity.name"></el-input>
        </el-form-item>
        <el-form-item label="序号" prop="order_number">
          <el-input-number
            v-model="classifyEntity.order_number"
            :min="0"
          ></el-input-number>
        </el-form-item>
        <el-form-item label="备注">
          <el-input v-model.trim="classifyEntity.remarks"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="saveClassifyEntity">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import entityList from "@/views/system/entity-list";
import { findAll, saveClassify, delClassify } from "@/api";
export default {
  name: "Index",
  components: { entityList },
  data() {
    return {
      editShow: false,
      active: "",
      list: [],
      classifyEntity: {
        name: "",
        order_number: 0,
        remarks: "",
      },
      rules: {
        name: [{ required: true, message: "请输入分类名称", trigger: "blur" }],
        order_number: [
          { required: true, message: "请输入序号", trigger: "blur" },
        ],
      },
      dialogVisible: false,
    };
  },
  mounted() {
    this.getList();
  },
  methods: {
    getTable(item) {
      if (item) {
        this.active = item.id;
        this.activeObj = item;
        // this.$refs.entityList.newEntityProps.classify_id =
        this.$refs.entityList.queryForm.classify_id = item.id;

        this.$refs.entityList.getEntityList();
      } else {
        this.$refs.entityList.queryForm.classify_id = null;
      }
    },
    async getList() {
      const { t } = await findAll();
      this.list = t;
      this.getTable(t[0]);
    },
    async saveClassifyEntity() {
      const { state, msg } = await saveClassify(this.classifyEntity);
      this.$baseMessage(msg, state);
      this.dialogVisible = false;
      this.getList();
    },
    handleAdd() {
      this.classifyEntity = this.$options.data().classifyEntity;

      if (this.list.length > 0) {
        this.classifyEntity.order_number =
          this.list[this.list.length - 1].order_number + 1;
      } else {
        this.classifyEntity.order_number = 1;
      }
      this.dialogVisible = true;
    },
    handleEdit(item, val) {
      if (val == "a") {
        this.classifyEntity = { ...item };
        this.dialogVisible = true;
      } else {
        this.$confirm("此操作将永久删除该分类, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }).then(async () => {
          const { state, msg } = await delClassify({ id: item.id });
          this.$message({
            type: state,
            message: msg,
          });
          this.getList();
        });
      }
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(() => {
          done();
        })
        .catch(() => {});
    },
  },
};
</script>

